import pageHeader from '../../../components/pageHeader'
export default {
  components: {
    pageHeader
  },
  data () {
    return {
      howGot: [
        {
          theme: '如何获取积分？',
          child: [
            {
              header: '注册成功',
              list: [
                {
                  title: '新用户注册成功',
                  addNum: 20
                }
              ]
            },
            {
              header: ' 完善个人主页',
              list: [
                {
                  title: '首次填写所有个人主页信息',
                  addNum: 20
                }
              ]
            },
            {
              header: '发布优质内容',
              list: [
                // {
                //   title: '首次发内容',
                //   addNum: 20
                // },
                {
                  title: '发布内容优质被加精',
                  addNum: 300
                }
              ]
            }
          ]
        }
      ],
      howUse: [
        {
          header: '如何使用积分？',
          child: [
            {
              title: '参与抽奖',
              list: [
                {
                  content: '进入「惊喜」中心，使用积分参与【积分抽奖】，赢取品质包包。'
                }
              ]
            },
            {
              title: '兑换礼品 ',
              list: [
                {
                  content: '进入「惊喜」中心，使用积分进行【兑换礼品】。'
                }
              ]
            },
            {
              title: '参与官方活动',
              list: [
                {
                  content: '进入「活动」频道，使用积分报名参加光芒官方线下活动，部分活动现场签到可给予相应积分奖励。(以当期活动说明为准)'
                }
              ]
            }
          ]
        }
      ],
      positiveInteraction: [
        {
          header: '积极社区互动',
          child: [
            {
              title: '每日签到',
              list: [
                {
                  content: '第1天签到',
                  addNum: 5,
                  type: 'default'
                },
                {
                  content: '第2天签到',
                  addNum: 10
                },
                {
                  content: '第3天签到',
                  addNum: 15
                }
              ],
              warning: '中断则从第一天获取积分规则计算'
            },
            {
              title: '喜欢一次',
              list: [],
              warning: '中断则从第一天获取积分规则计算'
            },
            {
              title: '每日签到',
              list: [
                {
                  content: '第1天签到',
                  addNum: 5,
                  type: 'default'
                },
                {
                  content: '第2天签到',
                  addNum: 10
                },
                {
                  content: '第3天签到',
                  addNum: 15
                }
              ],
              warning: '中断则从第一天获取积分规则计算'
            }
          ]
        }
      ],
      isFailure: [
        {
          header: '积分是否会失效？',
          child: [
            {
              list: [
                {
                  content: '积分有效期为5年，自积分入账年起至第五个公历年的\n' +
                    '12月31日止，逾期积分将自动失效。 按照积分入账先后顺序，优先消耗先入账的积分。' +
                    '<br />例：2018年12月31日（含）前发放的积分统一从2018年12月31日开始计算有效期，2023年12月31日之后过期失效。'
                }
              ]
            }
          ]
        }
      ]
    }
  }
}



